import React, { Component } from 'react';
import { Layout } from 'antd';
import './App.css';
import AppHeader from './components/Header.jsx';
import Loader from './components/Loader.jsx';
import DataInput from './components/Form.jsx';
import DataDisplay from './components/Table.jsx';
import AppFooter from './components/Footer.jsx';

const DEBUG = false;

const moralis = require('moralis');

const { Content } = Layout;

class App extends Component {
  constructor(props) {
    super(props);
    const stateObject = {
      api: {},
      events: { columns: [], data: [] },
      loading: true,
      percentLoaded: 50,
    };
    this.state = stateObject;
  }

  componentDidMount() {
    this.setState({ loading: false, percentLoaded: 0 });
  }

  // Make loading true if percentLoaded is specified. Or unless false is specified?
  dataLoading = ({ loading, percentLoaded }) => {
    this.setState({ loading, percentLoaded });
  };

  dataLoaded = ({ loading, percentLoaded }, events) => {
    this.setState({ events, loading, percentLoaded });
  };

  render() {
    const { events, loading, percentLoaded } = this.state;

    const { columns, data } = events;

    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Layout className="site-layout">
          <AppHeader className="site-layout-background" />
          <Content style={{ margin: '0 1rem' }}>
            {false ? (
              <Loader percent={percentLoaded} status="active" />
            ) : (
              <div
                className="site-layout-background"
                style={{ padding: 24, minHeight: 360 }}>
                <DataInput
                  dataLoading={this.dataLoading}
                  dataLoaded={this.dataLoaded}
                />
                <DataDisplay columns={columns} data={data} loading={loading} />
              </div>
            )}
          </Content>
          <AppFooter style={{ textAlign: 'center' }} />
        </Layout>
      </Layout>
    );
  }
}

export default App;
